import React from 'react'
import './header.css'
import PersonIcon from '@mui/icons-material/Person';//profile icon imported from material ui 
import ForumIcon from '@mui/icons-material/Forum';
import { IconButton } from '@mui/material';
import { Link,  useHistory  } from 'react-router-dom';
import ArrowBackIosIcon from '@mui/icons-material/ArrowBackIos';

function Header({backButton}){

   const history = useHistory()
    return (
        <div className='header'>
            {backButton ?(
                <IconButton onClick={() => history.replace(backButton)}>
                <ArrowBackIosIcon fontSize='large' className='header__backbutton' />
                </IconButton>
            ): (
                <IconButton>
            <PersonIcon className='header__icon' fontSize='large' />
          </IconButton>

            )}

          

           <Link to='/'>
           <img className='header__logo'  src="data:image/png;base64," alt="tinder logo" />
          </Link>

          <Link to="/chat">
          <IconButton>
           <ForumIcon className='header__icon' fontSize='large' />
         </IconButton>
         </Link>
        </div>
    )
}
 
export default Header